import axios from 'axios'

const state = {
    list: [],
    banner: [],
    loading: false,
    finished: false,
    refreshing: false
}

const actions = {
    home: ({state, commit}, {_page, _limit = 14, q, _name}) => {
        axios({url: "/news/home", params: {_limit, q, _name, _page}}).then(res => {
            commit('home', {data: res.data, _limit})

        })
    },
    banner: ({state, commit}) => {
        axios({url: "/news/banner"}).then(res => {
            commit("banner", res.data.data)
        })
    }
}

const mutations = {
    home(state, payload) {
        setTimeout(() => {
            if (payload.data.err) {
                state.finished = true
                state.refreshing && (state.refreshing = false);
            } else {
                if (state.refreshing) {
                    state.list = [];
                    state.refreshing = false;
                }
                state.list = [...state.list, ...payload.data.data]
                // 加载状态结束
                state.loading = false;
                // 数据全部加载完成
                state.finished = payload.data.data.length < payload._limit
            }
        }, 500)

    },
    banner(state, payload) {
        state.banner = payload
    },
    load(state, payload) {
        state.loading = payload
    }
}

export default {
    namespaced: true,
    state, actions, mutations
}