import axios from 'axios'
const state = {
    list: [],
    loading: false,
    finished: false,
    refreshing: false
}

const actions={
    follow: ({state, commit}, {_page, _limit = 14}) => {
        axios({url: "/news/follow", params: {_page, _limit}}).then(res => {
            commit('follow', {data: res.data, _limit})
        })
    },
}

const mutations={
    follow(state,payload){
        setTimeout(() => {
            if (payload.data.err) {
                state.finished = true
                state.refreshing && (state.refreshing = false);
            } else {
                if (state.refreshing) {
                    state.list = [];
                    state.refreshing = false;
                }
                state.list = [...state.list, ...payload.data.data]
                // 加载状态结束
                state.loading = false;
                // 数据全部加载完成
                state.finished = payload.data.data.length < payload._limit
            }
        }, 500)
    }
}

export default {
    namespaced:true,
    state,actions,mutations
}