import Vue from 'vue'
import VueRouter from 'vue-router'
import axios from "axios"

const Header = ()=>import("../components/header.vue")
const Home =()=>import("../views/Home.vue")
const Follow =()=>import("../views/follow.vue")
const Column =()=>import("../views/column.vue")
const Mydoc = ()=>import("../views/mydoc.vue")
const NoPage = ()=>import("../views/nopage.vue")
const Detail = ()=>import("../views/detail.vue")
const Reg = ()=>import("../views/reg.vue")
const Login = ()=>import("../views/login.vue")



Vue.use(VueRouter)

const routes = [
    {
        path: "/",
        component: Header,
        children: [
            {
                path: '/home',
                name: 'Home',
                component: Home,
            }, {
                path: "follow",
                name: "follow",
                component: Follow,
            }, {
                path: "Column",
                name: "Column",
                component: Column
            },
        ]
    },
    {
        path: "/mydoc",
        name: "Mydoc",
        component: Mydoc
    }, {
        path: "/detail/:id",
        component: Detail,
        name: "detail"
    }, {
        path: "/reg",
        component: Reg,
        name: "reg"
    }, {
        path: "/login",
        component: Login,
        name: "login"
    },{
        path: "*",
        name: "NoPage",
        component: NoPage
    }

]

const router = new VueRouter({
    mode: 'history',
    routes
})

router.beforeEach((to, from, next) => {
    let path = to.path;
    if (/login|reg/.test(path)) {
        next()
    } else {
        axios({
            url: '/is-token'
        }).then(res => {
            if (res.data.err) {
                next("/login")
            } else {
                next()
            }
        })
    }
})

export default router
