import axios from 'axios'
import TimeChange from '../../ults/time'

const state = {
    list: {}
}
const actions = {
    detail: async ({state, commit}, {path, id} = {}) => {
        const {data} = await axios({url: `/news/${path}/${id}`})
        commit('detail', data)
    }
}
const mutations = {
    detail(state, payload) {
        state.list = payload.data[0]
    }
}
const getters = {
    Time(){
        return TimeChange(state.list.time)
    }
}

export default {
    namespaced: true,
    state, actions, mutations,getters
}