<template>
  <div id="app">
    <!--    <my-header/>-->
    <my-loading v-if="$store.state.loading"></my-loading>
    <router-view></router-view>
    <my-footer v-if="$store.state.bottom"/>

  </div>
</template>
<script>
import MyFooter from "./components/footer.vue"
import MyLoading from "./components/loading.vue";
import {mapMutations} from 'vuex'

export default {
  name: 'app',
  components: {
    // MyHeader,
    MyFooter,MyLoading
  },
  data() {
    return {
      // top:true,
      bottom: true
    }
  },
  beforeMount() {
    const path = this.$route.path
    if (/home|follow|column/.test(path)) {
      // this.top = true;
      this.bottom = true;
    } else if (/detail|reg|login/.test(path)) {
      this.top = false;
      this.bottom = false;
    }
  },
  watch: {
    $route: function (val) {
      if (/home|follow|column/.test(val.path)) {
        // this.top = true;
        // this.bottom = true;
        this.$store.commit('bottomChange',true)
      } else if (/detail|reg|login/.test(val.path)) {
        // this.top = false;
        // this.bottom = false;
        this.$store.commit('bottomChange',false)

      }

    }
  }
}
</script>

